body {
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  line-height: 1.42; }

html, body {
  width: 100%;
  height: 100%;
  min-height: 100%;
  font-size: 14px; }

.wrapper {
  min-height: 100%;
  overflow: hidden;
  position: relative; }

.wrapper:after {
  content: "";
  display: block; }

a {
  text-decoration: none; }

ul {
  padding: 0;
  margin: 0; }

li {
  list-style: none; }

p {
  margin-top: 0; }

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  font-weight: normal; }

*:focus {
  outline: none; }

