.navbar-toggler:focus, .navbar-toggler:active {
  outline: none;
  box-shadow: none; }

.section-title {
  color: #1d1d1d;
  font-size: 3.125rem;
  font-weight: 600;
  line-height: 48px;
  margin-bottom: 2rem; }

.section-desc {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 28px;
  color: #787878; }

.header {
  background: #e9e9e9;
  padding: 1.25rem 0; }

.nav-item {
  margin-right: 0.9375rem; }

.nav-link {
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  font-size: 0.875rem;
  color: #191919;
  font-weight: 600;
  line-height: 24px; }
  .nav-link:hover {
    color: #10c9c3; }
  .nav-link:active {
    color: #10c9c3; }

.main {
  background-color: #e9e9e9;
  height: 90vh;
  margin-bottom: 6.25rem; }

.main-title_text {
  color: #1d1d1d;
  font-size: 3.125rem;
  font-weight: 600;
  line-height: 48px;
  margin-bottom: 2rem; }

.main-title_desc {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 28px;
  color: #787878;
  max-width: 29.6875rem;
  margin-bottom: 2rem; }

.main-tittle_btn {
  background: #10c9c3;
  color: white;
  font-size: 0.75rem;
  font-weight: 500;
  padding: 1.4375rem 2.8125rem !important; }
  .main-tittle_btn:hover {
    background: #191919; }
  .main-tittle_btn:active {
    background: #191919; }

.about {
  height: 50vh;
  margin: 6.25rem 0; }

.about-title {
  text-align: center; }

.slills {
  height: 60vh;
  background-color: #f8f9f9; }

.skills-left {
  background-color: #f8f9f9; }

.skills-title-text {
  padding: 10.3125rem 0 2.5625rem 0; }

.slills-title__list {
  padding: 0 0 10.3125rem 0; }

.skills-title__item-txt {
  font-family: Montserrat;
  font-weight: 500;
  color: #000;
  font-size: 1rem;
  line-height: 30px;
  margin-bottom: 0.75rem; }

.skills-title__item-img {
  margin-bottom: 2.5625rem; }

.skills-right {
  background-color: #e9e9e9; }

.parallax {
  height: 25vh;
  background-color: #10c9c3; }

.parallax__item {
  margin-right: 4.375rem; }

.parallax__item-img {
  width: 41px;
  height: 41px; }

.parallax-text {
  font-family: Montserrat;
  color: #fff; }

.parallax__item-title {
  margin-left: 3.125rem; }

.parallax__item-title-text {
  font-weight: 600;
  font-size: 1.25rem;
  margin-bottom: 1rem; }

.parallax__item-desc {
  font-size: 0.875rem;
  font-weight: 500;
  white-space: nowrap; }

.owl-carousel img {
  width: inherit !important;
  height: inherit !important; }

@media screen and (max-width: 1200px) {
  html {
    font-size: 16px; }
  .parallax__item-desc {
    white-space: normal; } }

@media screen and (max-width: 992px) {
  html {
    font-size: 0.9375rem; }
  .nav-item {
    margin-right: 0.625rem; }
  .skills-right {
    height: 60vh; } }

@media screen and (max-width: 768px) {
  html {
    font-size: 0.875rem; } }

@media screen and (max-width: 650px) {
  html {
    font-size: 0.875rem; } }

@media screen and (max-width: 480px) {
  html {
    font-size: 0.75rem; } }
